<template>
	<div class="stories--wrapper">
		<back-button title="Stories"
		@close="$router.push({name: 'Tasks'})"/>
		<p class="small-text">All homework examples appear here.</p>
		<div v-show="!isOpenStorie" class="stories--items">
			<div class="stories--item" @click.prevent="openStorie()">
				<img class="stories--item__img" src="/img/storie-item-bg.png">
				<div class="stories--item__inner">
					<p class="stories--item__title">To do list for you day</p>
					<p class="stories--item__count">Task 1</p>
				</div>
			</div>
			<div class="stories--item" @click.prevent="openStorie()">
				<img class="stories--item__img" src="/img/storie-item-bg2.png">
				<div class="stories--item__inner">
					<p class="stories--item__title">Do a Workout for a 20 min</p>
					<p class="stories--item__count">Task 2</p>
				</div>
			</div>
			<div class="stories--item" @click.prevent="openStorie()">
				<img class="stories--item__img" src="/img/storie-item-bg.png">
				<div class="stories--item__inner">
					<p class="stories--item__title">Find Motivational Quotes</p>
					<p class="stories--item__count">Task 3</p>
				</div>
			</div>
		</div>
		<div v-show="isOpenStorie" class="storie" :style="`height: ${__SCREEN_HEIGHT__}px`">
			<div class="storie--img">
				<img src="/img/storie-img.png">
			</div>
			<div class="storie--controls">
				<header>
					<div class="storie--progress">
						<div class="storie--progress__inner" :style="`width: ${storieProgress}%`"></div>
					</div>
					<div class="d-flex justify-content-end">
						<a href="#" @click="closeStorie()">
							<img src="/img/close-button.svg">
						</a>
					</div>
				</header>
			</div>
			<div class="storie--content">
				<div class="storie--content__item">Your task is to make and drink smoothy. Let's go!</div>
				<div class="text-center uppercased-text">
					SWIPE TO SEE OTHER USERS`S HOMEWORK <br>
					<img class="storie--content__arrow" src="/img/arrow-down_bold.svg">
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	export default {
		data() {
			return {
				storieProgress: 0,
				isOpenStorie: false,
				storieInterval: null
			}
		},
		created() {
			
		},
		methods: {
			openStorie() {
				const storieDuration = 10; //длительность показа стори в секундах
				const storieFrameRefreshRate = 10; // обновление прогрессбара раз в storieFrameRefreshRate миллисекунд

				this.isOpenStorie = true;
				this.storieInterval = setInterval(() => {

					this.storieProgress = this.storieProgress + (storieFrameRefreshRate/(storieDuration*10));
					if(this.storieProgress >= 100) {
						clearInterval(this.storieInterval);
						this.storieProgress = 0;
						this.closeStorie();
					}
				},storieFrameRefreshRate)

				
			},
			closeStorie() {
				this.isOpenStorie = false;
				this.storieProgress = 0;
				clearInterval(this.storieInterval);
			}
		}
	}
</script>
<style lang="scss" scoped>
	
</style>